<template>
  <div class='blocks'>
      
  </div>
</template>

<script>
import Button from './button.vue';
export default {
  name: 'daily-block',
  props: {
      direct: {
          type: String,
          required: true
      }
  },
  data () {
    return {
        date: this.GetDate(),
        options: ["traffic","weather","Incidents"]
    }
  },
  methods: {
      GetDate: function() {
          var today = new Date();
        var dd = today.getDate();
        var mm = today.getMonth() + 1; //January is 0!

        var yyyy = today.getFullYear();
        if (dd < 10) {
            dd = '0' + dd;
        } 
        if (mm < 10) {
            mm = '0' + mm;
        } 
        var today = dd + '/' + mm + '/' + yyyy;
        return today;
      }
  }
}
</script>

<style scoped>
.blocks {
    background: rgb(226, 228, 121);
}
</style>


